import React from 'react';
import Falcor from 'falcor';
import ShowList from 'common/show-list/show-list.jsx';
import Gallery from 'common/gallery/gallery.jsx';
import Title from 'common/title/title.jsx';
import Guide from 'lib/guide';
import Toolbar from 'common/toolbar/toolbar';

export default React.createClass({
  getInitialState(){
    return {
      shows : [],
    }
  },

  componentWillMount() {
    this.getShows();
  },

  getShows() {
    let guide = new Guide();
    guide.init().then(()=>{
      this.setState({
        shows: guide.getCurrentShowsByRating()
      });
    })
  },

    render() {
      console.log(this.state.shows);
      return (
        <div>
        <Title title="Top Rated" cover="http://www.popsci.com/sites/popsci.com/files/styles/xl_1x_/public/14262028-06bmbtvserierdub-rse-100626jpg.jpeg?itok=wYWsK-5M"/>

      <Gallery>
        <ShowList shows={this.state.shows}/>
        </Gallery>
      </div>
      )
    }
});



